:root {
  --color-primary: #5c7cfa;
  --color-primary-dark: #4263eb;
  --color-primary-alpha: #5c7cfa50;

  --body-color: #495057;
  --body-bg: #f8f9fa;

  --border-color: #dee2e6;
}

body {
  max-width: 60rem;
  margin-left: auto;
  margin-right: auto;
  padding-left: 2rem;
  padding-right: 2rem;
  color: var(--body-color);
  background: var(--body-bg);
  font-family:
    system-ui,
    -apple-system,
    "Segoe UI",
    Helvetica,
    Arial,
    sans-serif;
  line-height: 1.5;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
}

a {
  color: var(--color-primary);
  text-decoration: none;
  transition: color 0.3s;
}

a:hover {
  color: var(--color-primary);
  text-decoration: underline;
}

h1 {
  font-size: 2.5rem;
  text-align: center;
}

main[x-cloak] {
  opacity: 0;
}

main:not([x-cloak]) {
  opacity: 1;
  transition: opacity 0.3s;
}

textarea {
  -webkit-appearance: none;
  appearance: none;
  display: block;
  width: 100%;
  padding: 0.5rem 1rem;
  border: 1px solid var(--border-color);
  border-radius: 0.25rem;
  box-sizing: border-box;
  color: #33404d;
  line-height: inherit;
  font-size: 1rem;
  transition:
    border 0.3s,
    box-shadow 0.3s;
}

textarea:focus {
  box-shadow: 0 0 0 0.25rem var(--color-primary-alpha);
  border-color: var(--color-primary);
  outline: 0;
}

input {
  -webkit-appearance: none;
  appearance: none;
  display: block;
  width: 100%;
  padding: 0.5rem 1rem;
  border: 1px solid var(--border-color);
  border-radius: 0.25rem;
  box-sizing: border-box;
  color: #33404d;
  line-height: inherit;
  font-size: 1rem;
  transition:
    border 0.3s,
    box-shadow 0.3s;
}

input:focus {
  box-shadow: 0 0 0 0.25rem var(--color-primary-alpha);
  border-color: var(--color-primary);
  outline: 0;
}

details {
  margin: 1rem 0 2rem;
  border: 1px solid var(--border-color);
  border-radius: 0.25rem;
  transition: background 0.3s;
}

details[open] {
  background: #fff;
}

details summary {
  padding: 0.5rem 1rem;
  font-weight: 500;
  user-select: none;
  cursor: pointer;
  opacity: 0.8;
  outline: 0;
}

details div {
  padding: 1rem;
  border-top: 1px solid var(--border-color);
}

details small {
  margin: 0;
  font-size: 0.875rem;
  line-height: 2;
}

button {
  appearance: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 1rem;
  padding: 0.5rem 0.75rem;
  border: 1px solid var(--color-primary);
  border-radius: 0.25rem;
  background: var(--color-primary);
  color: #fff;
  font-size: 1rem;
  font-weight: 500;
  line-height: inherit;
  cursor: pointer;
  user-select: none;
  transition:
    border 0.3s,
    background 0.3s;
}

button:hover {
  border-color: var(--color-primary-dark);
  background: var(--color-primary-dark);
}

button:focus {
  box-shadow: 0 0 0 0.25rem var(--color-primary-alpha);
  border-color: var(--color-primary);
  outline: 0;
}

button:disabled {
  background: var(--color-primary);
  border-color: var(--color-primary);
  opacity: 0.6;
  cursor: not-allowed;
}

button.loading::before {
  content: "";
  display: inline-block;
  margin-right: 0.5rem;
  border: 2px solid #fff;
  border-top-color: transparent;
  border-bottom-color: transparent;
  border-radius: 50%;
  width: 0.75rem;
  height: 0.75rem;
  animation: rotate 0.5s linear infinite;
}

footer {
  padding: 1rem;
  border-top: 1px solid var(--border-color);
  text-align: center;
  opacity: 0.5;
}

footer i {
  font-style: normal;
  color: #ff8787;
}

.success,
.error {
  margin-bottom: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  color: #fff;
  text-align: center;
  opacity: 1;
  transition: opacity 0.3s;
}

.success {
  border: 1px solid #12b886;
  background: #38d9a9;
}

.error {
  border: 1px solid #fa5252;
  background: #ff8787;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

/* 设置API URL选择器和自定义URL输入框的样式 */
select#api-url-select,
input#custom-url-input {
  width: 100%;
  height: 2.4rem;
  font-size: 1rem;
  background-color: #212121;
  margin-bottom: 0.5rem;
  padding: 0.5rem 0.75rem;
  color: #ffffff;
  border: none;
  padding: 0.5rem 0.75rem;
  border-radius: 0.25rem;
  margin-right: 10px;
  margin-top: 2vhpx;
  /* 添加了顶部边距 */
}

/* 下面的代码定义了结果表格的样式 */
#result-table {
  border-collapse: collapse;
  width: 100%;
  margin-top: 20px;
  font-size: 14px;
  border-radius: 5px;
  border: none;
  overflow: hidden;
}

/* 下面的代码定义了结果表格表头的样式 */
#result-table th {
  background-color: var(--color-primary);
  color: #fff;
  font-weight: 400;
  height: 20px;
  padding: 10px 10px;
  text-align: left;
  width: 25%;
  border: 1px solid #dcdcdc;
}

/* 下面的代码定义了结果表格数据单元格的样式 */
#result-table td {
  height: 20px;
  text-align: left;
  padding: 10px 10px;
  border: 1px solid #d3d3d3;
}

#result-table td:first-child {
  width: 30%;
}

/* 下面的代码定义了结果表格奇数行的背景颜色 */
#result-table tbody tr:nth-child(odd) {
  background-color: #fefefe;
}

/* 下面的代码定义了结果表格偶数行的背景颜色 */
#result-table tbody tr:nth-child(even) {
  background-color: #d7f5fd;
}

/* 下面的代码定义了结果表格表头的宽度 */
#result-table .table-header {
  width: 25%;
}

/* 下面的代码定义了结果表格数据单元格的宽度、字体加粗和颜色 */
#result-table .table-data {
  font-weight: bold;
  color: #333;
}

/* 下面的代码定义了结果表格数据单元格的样式 */
#result-table .status-error {
  height: 20px;
  text-align: center;
  padding: 10px 25px;
  border: 1px solid #dcdcdc;
}

/* 下面的代码定义了一个类名为status-ok的样式，用于设置成功状态的文本颜色 */
.status-ok {
  color: #2d8d2d;
}

/* 下面的代码定义了一个类名为status-error的样式，用于设置错误状态的文本颜色 */
.status-error {
  color: #ed0808;
}

#api-key-input {
  width: 100%;
  height: 100px;
  border: 1px solid #999;
  background-color: #fff;
}
